.woocommerce-marketing-recommended-extensions-card .woocommerce-card__body {
  padding: 12px; }

.woocommerce-marketing-recommended-extensions-card .components-spinner {
  float: none;
  margin: 45px auto 50px;
  display: block; }

.woocommerce-marketing-recommended-extensions-card__items {
  display: flex;
  flex-wrap: wrap; }
  @media (min-width: 601px) {
    .woocommerce-marketing-recommended-extensions-card__items > a {
      width: 50%; } }
  @media (min-width: 961px) {
    .woocommerce-marketing-recommended-extensions-card__items > a {
      width: 33.3%; }
    .woocommerce-marketing-recommended-extensions-card__items--count-1 > a {
      width: 100%; }
    .woocommerce-marketing-recommended-extensions-card__items--count-2 > a {
      width: 50%; }
    .woocommerce-marketing-recommended-extensions-card__items--count-4 > a {
      width: 25%; } }

.woocommerce-marketing-recommended-extensions-item {
  display: block;
  padding: 12px;
  text-decoration: none;
  position: relative; }
  .woocommerce-marketing-recommended-extensions-item h4 {
    color: #23282d;
    margin: -2px 0 5px;
    font-size: 16px;
