.woocommerce-marketing-slider {
  display: block;
  width: 100%;
  overflow: hidden; }
  .woocommerce-marketing-slider > div {
    width: 100%;
    white-space: normal;
    overflow: hidden;
    display: block;
    position: relative;
    height: 100%; }
  .woocommerce-marketing-slider__slide {
    top: 0;
    left: 0;
    width: 100%;
    transition: transform 300ms ease-in;
    position: relative; }
  .woocommerce-marketing-slider .slide-enter,
  .woocommerce-marketing-slider .slide-exit {
    position: absolute; }
  .woocommerce-marketing-slider.animate-right .slide-enter {
    transform: translateX(-100%); }
  .woocommerce-marketing-slider.animate-right .slide-enter-active {
    transform: translateX(0%); }
  .woocommerce-marketing-slider.animate-right .slide-exit {
    transform: translateX(0%); }
  .woocommerce-marketing-slider.animate-right .slide-exit-active {
    transform: translateX(100%); }
  .woocommerce-marketing-slider.animate-left .slide-enter {
    transform: translateX(100%); }
  .woocommerce-marketing-slider.animate-left .slide-enter-active {
    transform: translateX(0%); }
  .woocommerce-marketing-slider.animate-left .slide-exit {
    transform: translateX(0%); }
  .woocommerce-marketing-slider.animate-left .slide-exit-active {
    transform: translateX(-100%); }
  @media screen and (prefers-reduced-motion: reduce) {
    .woocommerce-marketing-slider .slide-enter-active,
    .woocommerce-marketing-slider .slide-exit-active {
      transition: none !important; } }
